:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: var(--accent);
  --bg-white: #ffffff;
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.1);
  --radius-lg: 8px;
}
.form-container {
  max-width: 450px;
  margin: 2rem auto;
  padding: 2.1rem 2rem;
  background: var(--bg-white);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  font-family: inherit;
}
.form-container h2 {
  margin-top: 0;
  color: var(--text-primary);
  font-size: 1.4rem;
  font-weight: 600;
  letter-spacing: 0.01em;
  margin-bottom: 1.2rem;
}
label {
  display: block;
  margin-bottom: 0.39rem;
  color: var(--text-secondary);
  font-size: 1rem;
  font-weight: 500;
}
input, select, textarea {
  width: 100%;
  padding: 0.5rem;
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-light);
  margin-bottom: 1rem;
  font-size: 1rem;
  transition: border 0.2s;
  background: var(--bg-white);
}
input:focus, select:focus, textarea:focus {
  border-color: var(--border-focus);
  outline: none;
  box-shadow: 0 0 0 1.5px var(--accent, #2563eb25);
}
.btn-primary {
  width: 100%;
  background: var(--accent);
  color: white;
  border: none;
  border-radius: var(--radius-lg);
  padding: 0.8rem 0;
  font-size: 1.01rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
  margin-top: 0.7rem;
}
.btn-primary:hover:not(:disabled), .btn-primary:focus:not(:disabled) {
  background: #2563eb;
  transform: translateY(-1px) scale(1.01);
}
.error {
  background: #fee2e2;
  color: #dc2626;
  border-radius: 5px;
  padding: 0.5rem 0.8rem;
  font-size: 0.95rem;
  margin-bottom: 0.5rem;
  border: 1px solid #dc2626;
}
.success {
  color: #15803d;
  background: #bbf7d0;
  border: 1px solid #34d399;
  border-radius: 5px;
  padding: 0.5rem 0.8rem;
  font-size: 0.95rem;
  margin-bottom: 0.5rem;
}
@media (max-width: 480px) {
  .form-container {
    padding: 1rem 0.5rem;
    margin: 0.5rem;
  }
  label, h2 {
    font-size: 1rem;
  }
}
