:root {
  --status-requested: #fde68a; /* yellow */
  --status-approved: #bbf7d0; /* green */
  --status-rejected: #fecaca; /* red */
  --status-completed: #dbeafe; /* blue */
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-muted: #9ca3af;
  --radius-lg: 8px;
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.1);
}
.appointment-list-container {
  max-width: 900px;
  margin: 2rem auto;
  background: white;
  padding: 2.2rem 1.5rem 2.2rem 1.5rem;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
}
.appointment-list-container h2 {
  margin-top: 0;
  color: var(--text-primary);
  font-size: 1.35rem;
  font-weight: 600;
}
.filters-row {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  margin-bottom: 1.4rem;
  flex-wrap: wrap;
}
.filters-row label {
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: 500;
  color: var(--text-muted);
}
.filters-row select {
  min-width: 130px;
  font-size: 1rem;
  border-radius: 6px;
  border: 1px solid #cbd5e1;
  padding: 0.28rem 0.4rem;
}
.table-container {
  overflow-x: auto;
}
.appointment-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
}
.appointment-table th, .appointment-table td {
  padding: 0.55rem 0.6rem;
  text-align: left;
  font-size: 1rem;
  max-width: 250px;
  vertical-align: top;
}
.appointment-table th {
  background: #f7fafe;
  color: #4477cb;
  border-bottom: 2px solid #e5e7eb;
}
.status-badge {
  display: inline-block;
  font-weight: 600;
  font-size: 0.98rem;
  text-transform: capitalize;
  border-radius: 5px;
  padding: 2px 9px;
  margin-right: 0.7rem;
}
.action-btn {
  border: none;
  border-radius: 5px;
  padding: 5px 13px;
  font-size: 0.98rem;
  font-weight: 500;
  margin-right: 4px;
  margin-bottom: 3px;
  cursor: pointer;
  transition: background 0.2s;
}
.approve { background: #bbf7d0; color:#14643D;border: 1px solid #22c55e; }
.approve:hover { background: #22c55e; color: white; }
.reject { background: #fecaca; color: #991b1b; border: 1px solid #f87171; }
.reject:hover { background: #f87171; color: white; }
.empty-state {
  text-align: center;
  color: var(--text-muted);
  font-style: italic;
  font-size: 1.08rem;
  padding: 2rem 0 1rem 0;
}
.error {
  color: #dc2626;
  background: #fee2e2;
  border: 1px solid #dc2626;
  border-radius: 5px;
  padding: 0.5rem 0.8rem;
  margin-bottom: 0.9rem;
  width: fit-content;
}
@media (max-width: 600px) {
  .appointment-list-container {
    padding: 1rem 0.2rem;
  }
  .filters-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.4rem;
  }
  .appointment-table th, .appointment-table td {
    padding: 0.2rem 0.25rem;
    font-size: 0.97rem;
  }
}
