:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-muted: #9ca3af;
  --radius-lg: 8px;
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.08);
  --main-bg: #f9fafb;
}
.app-main-bg {
  min-height: 100vh;
  background: var(--main-bg);
  padding: 0;
}
.main-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-bottom: 1px solid #e5e7eb;
  padding: 1.1rem 0 1.1rem 0;
  gap: 0.9rem;
  position: sticky;
  top: 0;
  z-index: 10;
}
.tab-btn {
  background: transparent;
  border: 2px solid var(--accent);
  color: var(--accent);
  padding: 0.62rem 1.4rem;
  font-size: 1rem;
  border-radius: 5.5px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.16s;
  outline: none;
}
.tab-btn.active {
  background: var(--accent);
  color: white;
  border-color: var(--accent);
  transform: translateY(-2px) scale(1.01);
}
.tab-btn:not(:last-child) {
  margin-right: 0.3rem;
}
.tab-content {
  margin-top: 1.3rem;
}
@media (max-width: 500px) {
  .main-nav {
    flex-direction: column;
    padding: 0.55rem 0;
    gap: 0.4rem;
  }
  .tab-btn {
    width: 98vw;
    max-width: 330px;
    font-size: 0.98rem;
  }
}
